<template>
    <div id="common-box">
        <div class="check-box">
            <h2 class="check-title">手机号验证</h2>
            <div class="check-form">
                <!-- 验证信息 -->
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
                    <el-form-item label="您的手机号" prop="inputTel">
                        <div class="mycheck-input">
                            <el-input v-model="ruleForm.inputTel"></el-input>
                            <el-button size="mini"  @click="sendTel">获取手机验证码</el-button>
                        </div>
                    </el-form-item>
                    <el-form-item label="手机验证码" prop="inputCode">
                        <el-input v-model="ruleForm.inputCode" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="success" @click="codeTel('ruleForm')">验证手机号</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            ruleForm:{
                inputTel:'',
                inputCode:''
            },
            rules:{
                inputCode:[
                    { required:true,message:'请输入手机验证码',trigger:'blur'}
                ]
            },
            userInfo:{}
        }
    },
    methods:{
        sendTel(){
            let that=this;
            this.$api.code({
                account: that.userInfo.userId,
                phone:that.ruleForm.inputTel
            })
            .then(res=>{
                if(res.err_code === 200){
                    that.$message(`手机验证码已发送至${that.ruleForm.inputTel},请注意查收`)
                }else{
                    return that.$message('验证码发送失败');
                }
            })
        },
        codeTel(formVal){
            let that=this;
            this.$refs[formVal].validate((valid)=>{
                if(valid){
                this.$api.userphone(
                    that.ruleForm.inputTel,
                    that.ruleForm.inputCode).then(res=>{
                        if(res.err_code === 100){
                            that.$message('手机号认证成功');
                        }else{
                            that.$message('手机号认证失败');
                        }
                    })
                }
            })
        },
        getUserInfo(){
            let that=this;
            that.$api.userinfo().then(res=>{
                that.ruleForm.inputTel=res.data.user.userPhone;
            })
        }
    },
    created(){
        this.getUserInfo();
    }
}
</script>

<style lang="scss" scoped>

</style>